import React from 'react'
import TypeIt from "typeit-react"
import './Intro.css'

const Intro = () => {
  return (
    <div className='intro'>
        <div className="i-left">
            <div className="i-name">
                <span>
                    <TypeIt options={{
                        strings: ["!<br/> <font color ='orange'> </font> !!!!!"],
                        speed: 150,
                        loop: false,
                        // loopDelay: [2500],
                        waitUntilVisible: true,
                    }}/>
                </span>
                <span>
                        .
                </span>
            </div>

        </div>
        <div className="i-right">
            
        </div>
    </div>
  )
}

export default Intro